import {
  AnimatedKeyboardAvoidingViewRef,
  FullScreenWrapper,
  MyKeyboardAvoidingView,
  ScreenWrapper,
  toBoolean,
  UIDateTimeInput,
  UIInput,
  UIText,
  UITextInput,
} from '@true/ui';
import React, {memo, useCallback, useEffect, useRef, useState} from 'react';
import {HeaderCreatePost} from '@/screens/CreatePostScreen/HeaderCreatePost';
import Animated, {
  runOnUI,
  useAnimatedRef,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
} from 'react-native-reanimated';
import {
  goBack,
  normalizeStringForSearch,
  styled,
  ToastService,
  useAutoToastError,
  useBoolean,
  useNavigationParams,
} from '@true/core';
import {ICreatePoll, initParamCreatePoll} from '@/store/updates/type';
import {getChannels, useChannelsByQuery} from '@/store/channels';
import {AnimatedBottomSpace} from '@/components/AnimatedBottomSpace';
import {globalStyles} from '@/utils/globalStyle';
import {useUpdates} from '@/store/updates';
import {DiscardDialog} from '@/components/DiscardDialog';
import {useAsyncFn} from '@/hooks/useAsyncFn';
import {translate} from '@/hooks/useLanguage';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {MenuItem} from '@/components/MenuItem';
import {requestCreatePoll, requestEditPoll} from '@/store/updates/function';
import {BottomSheetMenu} from '@/components/BottomSheets';
import {AvatarWithSelectChannel} from '@/components/AvatarWithSelectChannel';
import {PollOptions} from '@/screens/CreatePollScreen/PollOptions';
import {CheckboxWithText} from '@/components/CheckboxWithText';
import {UICheckBoxCollapse} from '@/components/UICheckboxCollapse';
import LoadingModal from '@/components/LoadingModal';
import {View} from 'react-native';
import moment from 'moment';

export interface CreatePollScreenParams {
  id: number;
}

const limitedAnswer = Array.from({length: 11}, (_, index) => ({
  label: index === 0 ? 'Unlimited' : String(index),
  value: index === 0 ? 'Unlimited' : String(index),
}));

export const CreatePollScreen = memo(function () {
  const {id} = useNavigationParams<CreatePollScreenParams>();
  const ref = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useAnimatedRef<any>();
  const params = useSharedValue<ICreatePoll>(initParamCreatePoll);
  const [visibleDialog, showDialog, hideDialog] = useBoolean();
  const bottomSheetRef = useRef<BottomSheetModal | null>(null);
  const channelsOption = useChannelsByQuery('joined').map(id => ({
    label: getChannels(id)?.name || 'Undefined channel',
    value: id,
  }));
  const [label, setLabel] = useState('');
  const poll = useUpdates(id)?.attachment;
  const optionsRef = useRef<
    Array<{
      id: string;
      value: string;
    }>
  >([]);

  useEffect(() => {
    if (channelsOption && channelsOption.length > 0) {
      const selectedChannel = channelsOption.find(
        item => item.value === params.value.channel_id.toString(),
      );
      if (selectedChannel) {
        setLabel(selectedChannel.label);
      }
    }
  }, [params, setLabel, channelsOption]);

  const limitedAnswerOption = limitedAnswer.map(item => ({
    label: item.label || 'Undefined channel',
    value: item.value,
  }));

  const onPressAddOption = useCallback(() => {
    console.log(optionsRef.current);
    if (
      params.value.has_limit &&
      params.value.limited_answers &&
      optionsRef.current.length === params.value.limited_answers
    ) {
      return;
    }
    if (optionsRef.current.length === 20) {
      return;
    }
  }, [optionsRef, params]);

  const onGoBack = useCallback(() => {
    goBack();
  }, []);

  const [{loading, error: createError}, onPressSubmit] =
    useAsyncFn(async () => {
      const _ops = [...optionsRef.current]
        // .sort((a, b) => (a - b > 0 ? 1 : -1))
        .map(item => item.value);
      const _params = {
        ...params.value,
        name: params.value.name,
        multi: params.value.multi ? 'on' : 'off',
        has_etime: params.value.has_etime ? 'on' : 'off',
        'etime-date': moment(params.value.etime * 1000).format('DD/MM/YYYY'),
        'etime-time': moment(params.value.etime * 1000).format('HH:mm'),
        has_stime: params.value.has_stime ? 'on' : 'off',
        'stime-date': moment(params.value.stime * 1000).format('DD/MM/YYYY'),
        'stime-time': moment(params.value.stime * 1000).format('HH:mm'),
        allow_update: params.value.allow_update ? 'on' : 'off',
        allow_added: params.value.allow_added ? 'on' : 'off',
        limited_answers: normalizeStringForSearch(
          `${params.value.limited_answers || '0'}`,
        ),
        channel_id: params.value?.channel_id,
        id: poll?.id,
        has_limit: params.value.has_limit ? 'on' : 'off',
      };

      if (!_params.name || !_params.name.trim()) {
        ToastService.show(
          translate('Poll name is required', {
            vi: 'Vui lng nhp vo tn bnh chn',
            en: 'Poll name is required',
          }),
        );
        return;
      }

      if (!_ops.filter(item => toBoolean(item.trim())).length && id == -1) {
        ToastService.show(
          translate('At least one vote', {
            vi: 'Phi c t nht mt bnh chn',
            en: 'At least one vote',
          }),
        );
        return;
      }

      if (
        _params['stime-date'] &&
        _params['stime-time'] &&
        _params['etime-date'] &&
        _params['etime-time'] &&
        _params['has_etime'] &&
        _params['has_stime'] &&
        _params['etime-date'] <= _params['stime-date']
      ) {
        ToastService.show(
          translate('Error in the start time and end time', {
            vi: 'Thi gian bt u phi nh hn thi gian kt thc',
            en: 'Error in the start time and end time',
          }),
        );
        return;
      }
      if (_params['has_limit'] && Number(_params['limited_answers']) > 10) {
        ToastService.show(
          translate('Limit number of answers is 10', {
            vi: 'Cu tr li ti a l 10',
            en: 'Limit number of answers is 10',
          }),
        );
        return;
      }
      if (id !== -1) {
        // @ts-ignore
        await requestEditPoll(_params);
        ToastService.show('Edit poll successfully');
      } else {
        // @ts-ignore
        await requestCreatePoll({
          ..._params,
          options: _ops,
        });
        ToastService.show('Create poll successfully');
      }
      goBack();
    }, [id, params]);

  const animatedActiveBtn = useDerivedValue(() => {
    return !!params.value.channel_id;
  }, []);

  const onChangeValue = useCallback(
    (key: string, value: any) => {
      runOnUI((_key: string, _value) => {
        'worklet';

        params.value = {
          ...params.value,
          [_key]: _value,
        };
      })(key, value);
    },
    [params],
  );

  const renderItem = useCallback(
    (item: any, index: number) => {
      return (
        <MenuItem
          key={index}
          title={item.label}
          onPress={() => {
            onChangeValue('channel_id', item.value);
            setLabel(item.label);
            bottomSheetRef.current?.dismiss();
          }}
        />
      );
    },
    [bottomSheetRef],
  );

  useAutoToastError(createError);

  useEffect(() => {
    let _params = {
      ...initParamCreatePoll,
    };
    if (poll) {
      _params = {
        channel_id: poll?.channel_id || 0,
        name: poll?.name || '',
        multi: Boolean(Number(poll?.config.multi)),
        has_etime: Boolean(Number(poll?.config.has_etime)),
        etime: poll?.config?.etime,
        has_stime: Boolean(Number(poll?.config.has_etime)),
        stime: poll?.config?.stime,
        allow_update: Boolean(Number(poll?.config.allow_update)),
        allow_added: Boolean(Number(poll?.config.allow_added)),
        has_limit: Boolean(Number(poll?.config.has_limit)),
        limited_answers: Number(poll?.config?.limited_answers || 3),
      };
    }
    params.value = _params;
  }, [id]);

  return (
    <FullScreenWrapper isGrey>
      <MyKeyboardAvoidingView ref={ref} scrollRef={scrollRef}>
        <LoadingModal isVisible={loading} />
        <HeaderCreatePost
          title={'Create a poll'}
          active={animatedActiveBtn}
          // id={id}
          onPress={onPressSubmit}
          loading={loading}
          onGoBack={onGoBack}
        />
        <SScrollView ref={scrollRef} keyboardShouldPersistTaps={'handled'}>
          <SContainer>
            <SWhiteBox>
              <View style={globalStyles.mb4}>
                <UIText.HeaderMedium>General information</UIText.HeaderMedium>
                <View style={globalStyles.mt12}>
                  <AvatarWithSelectChannel
                    bottomSheetRef={bottomSheetRef}
                    label={label}
                    typePost={'a poll'}
                  />
                </View>
              </View>
              <UIInput.Text
                title={'Poll name'}
                keyName={'name'}
                params={params}
                placeholder={'---'}
                defaultValue={''}
                required
              />
              <UITextInput
                title={'Poll content'}
                keyName={'content'}
                params={params}
                defaultValue={''}
                onChangeValue={onChangeValue}
              />
              <UIInput.UserPicker
                title={'Followers'}
                keyName={'followers'}
                params={params}
                onChangeValue={onChangeValue}
                multiple
                type={'user'}
              />
            </SWhiteBox>
            <SWhiteBox>
              {id == -1 ? (
                <PollOptions
                  optionsRef={optionsRef}
                  isMultipleSelection={poll ? poll.config.multi : false}
                  onPressAddOption={onPressAddOption}
                />
              ) : null}
            </SWhiteBox>
            <SWhiteBox>
              <UIText.HeaderMedium>Settings</UIText.HeaderMedium>
              <UICheckBoxCollapse
                title={'Multiple answers per user (MA)'}
                keyNameCheckbox={'multi'}
                params={params}
                childComponent={
                  <UIInput.Selector
                    keyName={'limited_answers'}
                    params={params}
                    title={'Limited answer'}
                    onChangeValue={onChangeValue}
                    options={limitedAnswerOption}
                  />
                }
              />
              <UICheckBoxCollapse
                title={'Enable started time'}
                keyNameCheckbox={'has_stime'}
                params={params}
                childComponent={
                  <UIDateTimeInput
                    keyName={'stime'}
                    params={params}
                    title={translate('Started vote')}
                    onChangeValue={onChangeValue}
                    mode={'datetime'}
                  />
                }
              />
              <UICheckBoxCollapse
                title={'Enable voting deadline'}
                keyNameCheckbox={'has_etime'}
                params={params}
                childComponent={
                  <UIDateTimeInput
                    keyName={'etime'}
                    params={params}
                    title={translate('Voting deadline')}
                    onChangeValue={onChangeValue}
                    mode={'datetime'}
                  />
                }
              />

              <UIText.HeaderMedium>Advanced settings</UIText.HeaderMedium>

              <CheckboxWithText
                params={params}
                keyName={'allow_update'}
                title={'Allow users to update their voting after voted'}
              />

              <CheckboxWithText
                params={params}
                keyName={'allow_added'}
                title={'Allow users to add new options to the voting'}
              />

              <UICheckBoxCollapse
                title={'Limited answers for multiple-answers voting'}
                keyNameCheckbox={'has_limit'}
                params={params}
                childComponent={
                  <UITextInput
                    keyName={'limited_answers'}
                    params={params}
                    title={translate('Limit number of answers') + ' (10)'}
                    onChangeValue={onChangeValue}
                    placeholder={translate('Limit number of answers') + ' (10)'}
                    keyboardType={'number-pad'}
                  />
                }
              />
            </SWhiteBox>
            <AnimatedBottomSpace />
          </SContainer>
        </SScrollView>
        <DiscardDialog
          title={'Discard change'}
          content={
            'You have made changes to this item that have not been saved. If you leave this page, your changes will be lost.'
          }
          visible={visibleDialog}
          onClose={hideDialog}
        />
        <BottomSheetMenu
          name={'channel'}
          ref={bottomSheetRef}
          items={channelsOption}
          renderMenuItem={renderItem}
          titleHeader={'Select channel'}
        />
      </MyKeyboardAvoidingView>
    </FullScreenWrapper>
  );
});

export default CreatePollScreen;

const SContainer = styled(Animated.View)`
  flex: 1;
  gap: 8px;
`;
const SWhiteBox = styled.View`
  margin-top: 8px;
  background-color: white;
  padding: 12px 16px;
  gap: 8px;
`;
const SScrollView = styled.ScrollView`
  flex: 1;
`;
