import {styled} from '@true/core';
import React, {RefObject, useCallback, useImperativeHandle} from 'react';
import Animated, {
  interpolate,
  SharedValue,
  useAnimatedReaction,
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from 'react-native-reanimated';
import {ViewStyle} from 'react-native';
import {memoForwardRef} from '@true/core';

interface Props {
  children?: any;
  contentStyle?: ViewStyle;
}

export interface AnimatedCollapseMethod {
  toggle: () => void;
  press: (value: number) => void;
}

export const AnimatedCollapseWrapper = memoForwardRef(
  (props: Props, ref: RefObject<AnimatedCollapseMethod>) => {
    const visible = useSharedValue(0);
    const animatedHeight = useSharedValue(0);
    const contentHeight = useSharedValue(0);

    const toggle = useWorkletCallback(() => {
      'worklet';
      if (visible.value == 1) {
        visible.value = 0;
        return;
      } else {
        visible.value = 1;
      }
    }, [visible]);

    const press = useCallback(
      (value: number) => {
        console.log(value);
        visible.value = value;
      },
      [visible],
    );

    useImperativeHandle(ref, () => ({
      toggle,
      press,
    }));

    useAnimatedReaction(
      () => ({
        _visible: visible.value,
        _contentHeight: contentHeight.value,
      }),
      (cur, prev) => {
        if (prev?._contentHeight == 0) {
          animatedHeight.value = 0;
          return;
        }
        if (cur._visible == 1) {
          animatedHeight.value = withTiming(cur._contentHeight, {
            duration: 200,
          });
        } else {
          animatedHeight.value = withTiming(0, {duration: 200});
        }
      },
    );

    const animatedContainerStyle = useAnimatedStyle(() => ({
      height: animatedHeight.value,
      opacity: interpolate(
        animatedHeight.value,
        [0, contentHeight.value],
        [0, 1],
      ),
    }));

    const onLayout = useWorkletCallback((e: any) => {
      'worklet';
      contentHeight.value = e.nativeEvent.layout.height;
    }, []);

    return (
      <SContainer>
        <SContent style={[{overflow: 'scroll'}, animatedContainerStyle]}>
          <Animated.View style={props.contentStyle} onLayout={onLayout}>
            {props.children}
          </Animated.View>
        </SContent>
      </SContainer>
    );
  },
);

const SContainer = styled(Animated.View)`
  //padding: 12px 16px;
`;

const SContent = styled(Animated.View)``;
