import React, {
  memo,
  Ref,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from 'react';
import {AnimatedCollapseList} from '@/components/AnimatedCollapseList';
import {SimplePollOption} from '@/screens/CreatePollScreen/SimplePollOption';
import {getUpdates} from '@/store/updates';
import {Icons} from '@true/asset';
import {styled} from '@true/core';
import {UIButton, UIText, UITextStyle} from '@true/ui';
import moment from 'moment/moment';

interface Props {
  isMultipleSelection: boolean;
  onPressAddOption: () => void;
  optionsRef: React.MutableRefObject<{id: string; value: string}[]>;
}

export const PollOptions = memo(
  ({isMultipleSelection, onPressAddOption, optionsRef}: Props) => {
    const [options, setOptions] = useState([
      {
        id: moment().valueOf().toString(),
        value: '',
      },
    ]);
    //
    useEffect(() => {
      if (options && optionsRef) {
        optionsRef.current = [...options];
      }
    }, [options]);
    const optionList = useMemo(() => {
      return [...options.values()];
    }, [options]);

    const onOptionChange = useCallback((value: string, id: string) => {
      console.log('xx');
      setOptions(prev =>
        prev.map(item => {
          if (item.id === id) {
            return {
              ...item,
              value,
            };
          }

          return item;
        }),
      );
    }, []);

    const onOptionDelete = useCallback((id: string) => {
      setOptions(prev => prev.filter(item => item.id !== id));
    }, []);

    const onPress = useCallback(() => {
      onPressAddOption();
      setOptions(prev =>
        prev.concat({
          id: moment().valueOf().toString(),
          value: '',
        }),
      );
    }, [onPressAddOption]);

    return (
      <SField>
        <STitle>Poll options</STitle>
        <AnimatedCollapseList>
          {options && options.length
            ? options.map((item, index) => {
                return (
                  <SimplePollOption
                    value={item.value || ''}
                    key={item.id}
                    id={item.id}
                    index={index}
                    onChange={onOptionChange}
                    onDelete={onOptionDelete}
                    autoFocus={index > 0}
                    isMultipleSelection={isMultipleSelection}
                  />
                );
              })
            : null}
        </AnimatedCollapseList>
        {optionList.length < 20 ? <BtnAddOption onPress={onPress} /> : null}
      </SField>
    );
  },
);

const SField = styled.View`
  padding-bottom: 12px;
  margin-top: 8px;
  background-color: ${p => p.theme.white};
`;

const STitle = styled(UIText.HeaderMedium)`
  color: ${p => p.theme.neutral1};
`;

const BtnAddOption = styled(UIButton).attrs(p => ({
  source: Icons.Outlined.PLUS,
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral2,
  },
  title: 'Add more poll option...',
  textStyle: {
    ...UITextStyle.body_medium_400,
    color: p.theme.neutral2,
    marginLeft: 8,
  },
}))`
  padding: 16px;
  border: 1px solid ${p => p.theme.neutral5};
  border-radius: 8px;
  margin-top: 8px;
`;
