import React, {
  memo,
  ReactComponentElement,
  useCallback,
  useEffect,
  useRef,
} from 'react';
import {SharedValue, useDerivedValue} from 'react-native-reanimated';
import {styled, useMounted} from '@true/core';
import {UICheckbox} from '@/components/UICheckbox';
import {UIButton} from '@true/ui';
import {
  AnimatedCollapseMethod,
  AnimatedCollapseWrapper,
} from '@/components/AnimatedCollapseWrapper';
import {View} from 'react-native';

interface Props {
  keyNameCheckbox: string;
  params: SharedValue<any>;
  childComponent: ReactComponentElement<any>;
  title: string;
}

export const UICheckBoxCollapse = memo(
  ({params, keyNameCheckbox, childComponent, title}: Props) => {
    const collapseRef = useRef<AnimatedCollapseMethod | null>(null);
    const mounted = useMounted(300);

    const animatedCheckbox = useDerivedValue(
      () => params.value[keyNameCheckbox],
      [keyNameCheckbox],
    );

    useEffect(() => {
      if (mounted) {
        collapseRef?.current?.press?.(params.value[keyNameCheckbox] || 0);
      }
    }, [mounted]);

    const onTick = useCallback(() => {
      const value = animatedCheckbox.value ? 0 : 1;
      collapseRef?.current?.press?.(value);

      params.value = {
        ...params.value,
        [keyNameCheckbox]: value,
      };
    }, [keyNameCheckbox]);

    return (
      <Wrapper>
        <SBtn onPress={onTick}>
          <UICheckbox value={animatedCheckbox} />
          <STitleCheckbox>{title}</STitleCheckbox>
        </SBtn>
        <AnimatedCollapseWrapper ref={collapseRef}>
          <View style={{marginTop: 8}}>{childComponent}</View>
        </AnimatedCollapseWrapper>
      </Wrapper>
    );
  },
);

const SBtn = styled(UIButton)`
  flex: 1;
  flex-direction: row;
`;

const STitleCheckbox = styled.Text`
  font-size: 14px;
  color: ${p => p.theme.neutral1};
  padding-left: 12px;
`;

const Wrapper = styled.View`
  flex: 1;
  margin: 8px 0;
`;
