import {RefObject, useCallback, useImperativeHandle} from 'react';
import styled from 'styled-components/native';
import Animated, {
  runOnJS,
  useAnimatedReaction,
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
  withDelay,
  withTiming,
} from 'react-native-reanimated';
import {LayoutChangeEvent, ViewStyle} from 'react-native';
import {interactManager, memoForwardRef} from '@true/core';
import {toBoolean} from '@true/ui';

interface Props {
  children?: any;
  contentStyle?: ViewStyle;
  onRemoveItem: () => void | Promise<any>;
}

export interface AnimatedCollapseItemMethod {
  remove: () => void;
}

export const AnimatedCollapseItem = memoForwardRef(
  (
    {children, contentStyle, onRemoveItem}: Props,
    ref: RefObject<AnimatedCollapseItemMethod>,
  ) => {
    const height = useSharedValue(0);
    const isRemoved = useSharedValue(false);

    const onLayout = useWorkletCallback(
      ({
        nativeEvent: {
          layout: {height: _height},
        },
      }: LayoutChangeEvent) => {
        height.value = _height;
      },
      [height],
    );

    const onRemove = useCallback(
      (value: number) => {
        if (!toBoolean(value)) {
          interactManager(onRemoveItem, 100);
        }
      },
      [onRemoveItem],
    );

    const animatedStyle = useAnimatedStyle(() => {
      return {
        height: height.value,
        overflow: 'scroll',
        width: '100%',
      };
    }, [height]);

    useAnimatedReaction(
      () => ({_height: height.value, _isRemoved: isRemoved.value}),
      (cur, prev) => {
        if (cur._isRemoved) {
          runOnJS(onRemove)(cur._height);
        }
      },
      [height],
    );

    const remove = useWorkletCallback(() => {
      isRemoved.value = true;
      height.value = withDelay(500, withTiming(0, {duration: 500}));
    }, []);

    useImperativeHandle(ref, () => ({
      remove,
    }));

    return (
      <SItemContainer style={animatedStyle}>
        <Animated.View style={contentStyle} onLayout={onLayout}>
          {children}
        </Animated.View>
      </SItemContainer>
    );
  },
);

const SItemContainer = styled(Animated.View)`
  //padding: 12px 16px;
`;
