import {memo, RefObject} from 'react';
import Animated, {
  SharedValue,
  useAnimatedReaction,
  useAnimatedStyle,
  useDerivedValue,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from 'react-native-reanimated';
import styled from 'styled-components/native';
import {LayoutChangeEvent, ViewStyle} from 'react-native';
import {memoForwardRef} from '@true/core';

interface Props {
  children?: any;
  contentStyle?: ViewStyle;
  height?: SharedValue<number>;
  type?: string;
}

interface AnimatedCollapseMethod {}

export const AnimatedCollapseList = memoForwardRef(
  (props: Props, ref: RefObject<AnimatedCollapseMethod>) => {
    const contentHeight = useSharedValue(0);
    const animatedHeight = useSharedValue(0);
    const {type} = props;

    const onLayout = useWorkletCallback(
      ({
        nativeEvent: {
          layout: {height: _height},
        },
      }: LayoutChangeEvent) => {
        contentHeight.value = _height;
        if (props.height) {
          props.height.value = _height;
        }
      },
      [contentHeight],
    );

    useAnimatedReaction(
      () => ({value: contentHeight.value}),
      (cur, prev) => {
        if (!prev?.value && cur.value) {
          animatedHeight.value = cur.value;
          return;
        }
        if (prev && cur.value !== prev?.value) {
          animatedHeight.value = withTiming(cur.value, {duration: 500});
        }
      },
      [contentHeight],
    );

    const _animatedHeight = useDerivedValue(() => {
      if (type && type === 'height') {
        return {height: animatedHeight.value};
      }

      return {maxHeight: animatedHeight.value};
    }, [type, animatedHeight]);

    const animatedContainerStyle = useAnimatedStyle(() => {
      return {
        ..._animatedHeight.value,
        overflow: 'scroll',
      };
    }, [animatedHeight, _animatedHeight]);

    return (
      <SContainer>
        <SContent style={[{overflow: 'scroll'}, animatedContainerStyle]}>
          <Animated.View style={props.contentStyle} onLayout={onLayout}>
            {props.children}
          </Animated.View>
        </SContent>
      </SContainer>
    );
  },
);

const SContainer = styled(Animated.View)`
  //padding: 12px 16px;
`;

const SContent = styled(Animated.View)``;
